import styled from 'styled-components/macro';
import LogoSvg from '../assets/img/logo.svg?include';
import {colors, spacing} from '../config/styles';

export default function Logo(props: {}) {
    return (
        <Container {...props}>
            <Svg dangerouslySetInnerHTML={{__html: LogoSvg}} />
        </Container>
    );
}

const Svg = styled.div`
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;

    svg {
        flex: 1 1 auto;
        max-width: 100%;
        max-height: 100%;
        display: block;
        ${colors.withVar('primary', 'fill')};
    }
`;

const Container = styled.div`
    overflow: hidden;
    position: relative;
    height: calc(100% - ${spacing.medium});
    max-width: 100%;
    max-width: 100px;
    min-width: 50px;
    margin: auto 0;
    cursor: pointer;
    flex: 0 1 100px;
    outline: none;

    &:not(:active):focus {
        opacity: 0.9;
    }
`;
